/**
 * Classes Buttons
 *
 * btn
 *
 */
.btn, .button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 0px 30px;
  font-weight: 600;
  font-size: 16px;
  line-height: 14px;
  border-radius: 999px;
  text-decoration: none;
  border: 2px solid transparent;
  text-transform: uppercase;
  height: 50px;
  letter-spacing: -0.02em;
  transition: all 0.12s linear;
  text-align: center;
}
.btn-opacity, .button-opacity {
  background-color: transparent;
  border-color: #DA1B5A;
}
.btn-opacity:hover, .button-opacity:hover {
  color: #fff;
  border-color: #DA1B5A;
  background-color: #DA1B5A;
}
.btn-gradient, .button-gradient {
  color: #fff !important;
  border: none;
  background: linear-gradient(110.3deg, #DA1B5A 34.05%, #E5AF2A 86.98%);
  position: relative;
  z-index: 1;
}
.btn-gradient::after, .button-gradient::after {
  content: "";
  position: absolute;
  left: 0px;
  top: 0px;
  right: 0px;
  bottom: 0px;
  background: linear-gradient(100.3deg, #FCC62B 34.05%, #DA1B5A 86.98%);
  opacity: 0;
  transition: all 2s ease-in-out;
  border-radius: 999px;
  z-index: -1;
}
.btn-gradient:active, .button-gradient:active {
  color: #fff;
}
.btn-gradient:hover, .button-gradient:hover {
  color: #fff;
}
.btn-gradient:hover::after, .button-gradient:hover::after {
  opacity: 1;
}
.btn-color-white, .button-color-white {
  color: #fff;
}
.btn-lg, .button-lg {
  height: 57px;
  line-height: 18px;
}
.btn span, .button span {
  position: relative;
  top: 2px;
}
.btn.green, .button.green {
  background-color: #DA1B5A;
  border-color: #DA1B5A;
  color: #fff;
}
.btn svg, .button svg {
  margin: 0 0 0 8px;
}

.swiper-button {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  border: 2px solid #DA1B5A;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: all 0.12s linear;
}
.swiper-button svg path {
  transition: all 0.12s linear;
}
.swiper-button:hover {
  background-color: #DA1B5A;
  border-color: #DA1B5A;
}
.swiper-button:hover svg path {
  fill: #fff;
}
.swiper-button-disabled {
  opacity: 0.25;
}
.swiper-button.absolute {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.swiper-button.absolute.swiper-button-prev {
  left: 0;
}
.swiper-button.absolute.swiper-button-next {
  right: 0;
}

