.team {

  &__popup {
    display: none;

    &-row {
      $right-width: 380px;

      display: flex;
      flex-wrap: wrap;
      @include media(md) {
        flex-direction: column;
      }

      &-left {
        width: calc(100% - #{$right-width});
        @include media(md) {
          width: 100%;
        }
      }

      &-right {
        width: $right-width;
        padding: 0 0 0 30px;
        @include media(md) {
          width: 100%;
          padding: 30px 0 0 0px;
        }
      }

    }

  }



}
