.page_title {
  $btngroup-width: 280px;
  padding: 25px 0 0 0;
  @include media(lg) {
    padding: 0;
  }

  .container {

    @include media(md) {
      padding: 0
    }
  }

  &__inner {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-end;
  }

  &__image {
    position: relative;
    z-index: 1;
    border-radius: 200px 0px 0px 0px;
    overflow: hidden;
    width: calc(100% - #{$btngroup-width});
    height: 600px;

    @include media(md) {
      height: 384px;
      width: 100%;
    }

    &.full {
      width: 100%;
    }

    &.mini {
      height: 450px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: top center;
    }
  }

  &__title {
    position: absolute;
    bottom: 0;
    z-index: 3;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 25px 50px 25px 0px;
    min-height: 142px;
    right: calc(#{$btngroup-width} + 20%);
    left: 0;
    @include media(md) {
      width: 100%;
      padding: 0 15px;
    }

    &::before {
      content: '';
      display: block;
      position: absolute;
      z-index: -1;
      background-color: $color-yellow-light;
      top: 0;
      bottom: 0;
      right: 0;
      left: -9999px;
      border-radius: 0px 100px 0px 0px;
    }

    &-h {
      color: #fff;
      margin: 0;
      font-weight: normal;
      font-size: 50px;
      line-height: 1.2;
      text-transform: uppercase;
      position: relative;
      top: 8px;

      @include media(md) {
        font-size: 32px;
        line-height: 32px;
        letter-spacing: -0.02em;

      }
    }
  }

  &__btngroup {
    width: $btngroup-width;
    min-height: 100px;
    background-color: $color-blue;
    border-radius: 0px 100px 0px 0px;
    padding: 65px 40px 80px 25px;
    @include media(md) {
      //display: none;
    }

    &.mini {
      padding-top: 30px;
      padding-bottom: 20px;
      @include media(md) {
        position: absolute;
        background-color: transparent;
      }
    }

    a {
      text-transform: uppercase;
      font-size: 30px;
    }

    .home__intro-link_end {
      position: static;
      padding: 0;
      border-radius: unset;
      background-color: transparent;
      width: 100%;

      > a {
        &:last-child {
          margin-bottom: 0;
        }
      }

      &-item {
        $size: 150px;
        $size-md: 80px;

        min-height: $size;
        @include media(md) {
          min-height: $size-md;
        }

        &::after {
          width: $size;
          height: $size;
          @include media(md) {
            top: -150%;
            left: -50%;
          }
        }
      }
    }
  }

}

.page:not(.home), {
  .page_title {

    &__btngroup {
      @include media(md) {
        //display: none;
      }

      padding-top: 30px;
      padding-bottom: 20px;
      @include media(md) {
        position: absolute;
        background-color: transparent;
      }

      a {
        text-transform: uppercase;
        font-size: 30px;
      }

      .home__intro-link_end {
        position: static;
        padding: 0;
        border-radius: unset;
        background-color: transparent;
        width: 100%;
        @include media(md) {
          display: flex;
          flex-direction: column;
        }

        > a {
          &:last-child {
            margin-bottom: 0;
          }
        }

        &-item {
          $size: 150px;
          $size-md: 100px;

          min-height: $size;
          @include media(md) {
            min-height: $size-md;
          }

          &::after {
            width: $size;
            height: $size;
            @include media(md) {
              top: -50%;
              left: 100%;
              width: 50px;
              height: 50px;
            }
          }

          .title {
            @include media(md) {
              top: -50px;
              left: 50%;
            }
          }
        }
      }
    }
  }

}
