.sidebar {
  $left-width: 354px;

  display: flex;
  margin: 0 0 50px 0;
  padding: 70px 0;


  @include media(md) {
    padding: 38px 0;
    margin: 0;
  }

  &__left {
    width: $left-width;

    @include media(md) {
      display: none;
    }
  }

  &__right {
    width: calc(100% - #{$left-width});
    padding: 30px 0 0 70px;
    @include media(md) {
      padding: 0;
      width: 100%;
    }

    &-title {
      font-weight: bold;
      font-size: 42px;
      line-height: 48px;
      text-transform: uppercase;

      span {
        font-weight: normal;
        display: block;
      }
    }

    .text__style {
      p {

        @include media(lg) {
          margin-bottom: 5%;

        }
        @include media(md) {
          font-size: 16px;
          line-height: 24px;
          letter-spacing: -0.02em;
          margin-bottom: 0;
        }

        a {
          transition: all 0.18s linear;
          text-decoration: underline;

          &:hover {
            color: $color-yellow;
          }

          &.button {

            margin-top: 3%;
            @include media(xl) {
              margin-top: 5%;

            }
            @include media(lg) {
              margin-bottom: 5%;

            }
            @include media(md) {
              width: 100%;
            }
          }
        }
      }

      h4 {
        margin-bottom: 2%;
      }

      .table-responsive {
        $padding-lg: 8px;
        @include media(lg) {
          overflow-x: scroll;
          width: 100%;
        }
        @include media(md) {
          overflow-x: scroll;
          width: 100%;
        }

        th {
          background-color: $color-yellow-light;
          padding: $padding-lg;
          color: $color-white;
          //border: 1px solid $background-blue;
        }

        td {
          padding: $padding-lg;
          background-color: $color-white;

          a {
            transition: all 0.18s linear;
            text-decoration: underline;

            &:hover {
              color: $color-yellow;
            }
          }
        }
      }

      ul {

        li {

          a {
            transition: all 0.18s linear;
            text-decoration: underline;

            &:hover {
              color: $color-yellow;
            }
          }


        }

        &.contact-form-list {
          li {
            @include media(md) {

              display: flex;
              flex-direction: column-reverse;
              margin-bottom: 5%;
            }

            input[type=text], textarea {
              border-radius: 999px;

              @include media(md) {
                width: 100%;

              }
            }

            textarea {
              @include media(md) {
                border-radius: 10px;
              }
            }

            input[type=submit] {
              border-radius: 999px;

              @include media(md) {
                color: $color-white;
                background-color: $color-blue-light;
                border: $color-blue-light 2px solid;
                height: 57px;
              }
            }

            br {
              @include media(md) {

                display: none;
              }
            }
          }
        }
      }

      h4 {
        a {
          color: $color-red;
        }
      }
    }

  }

  &__menu {

    &-list {

      > li {
        background-color: #fff;

        &:not(:last-child) {
          margin: 0 0 5px 0;
        }

        &:first-child {
          border-radius: 0px 60px 0px 0px;

          > a {
            border-radius: 0px 60px 0px 0px;
          }
        }

        &.current_page_item {
          > a {
            color: $color-red;

            &::after {
              background-image: url('../images/left_menu_arrow_yellow.svg');
            }
          }

          > ul {
            display: block;
          }

          &.menu-item-has-children {
            > a {
              &::after {
                transform: translateY(-50%) rotate(90deg);
              }
            }
          }
        }

        &:not(.current_page_item) {
          > a {
            &:hover {
              color: rgba(28, 51, 96, 0.6);

              &::after {
                background-image: url('../images/left_menu_arrow_yellow.svg');
              }
            }
          }
        }

        > a {
          display: flex;
          justify-content: flex-start;
          min-height: 90px;
          align-items: center;
          padding: 0 70px 0 30px;
          font-weight: normal;
          font-size: 21px;
          line-height: 1.2;
          color: $color-blue;
          position: relative;
          text-transform: uppercase;

          &::after {
            $size: 38px;

            content: '';
            display: block;
            position: absolute;
            width: $size;
            height: $size;
            background: url('../images/left_menu_arrow_red.svg') center center no-repeat;
            right: 17px;
            top: 50%;
            transform: translateY(-50%);
          }

          &:hover {


          }
        }

        > ul {
          padding: 0 0 30px 30px;
          position: relative;
          z-index: 3;
          margin-top: -15px;
          display: none;

          li {
            &.current_page_item {
              > a {
                color: $color-red;
              }
            }
          }

          a {
            text-transform: none;
            font-weight: normal;
            font-size: 21px;
            line-height: 1.4;

            &:hover {
              color: rgba(28, 51, 96, 0.6);
            }
          }
        }

        &.page_item_has_children.current_page_item,
        &.page_item_has_children.current_page_parent {

          > ul {
            display: block;
          }

          > a {
            &::after {
              transform: translateY(-50%) rotate(90deg);
              background-image: url('../images/left_menu_arrow_yellow.svg');
            }
          }

        }


        &.page_item_has_children {
          &:hover {
            > a {
              &::after {
                transform: translateY(-50%) rotate(90deg);
                background-image: url('../images/left_menu_arrow_yellow.svg');
              }
            }

            > ul {
              display: block;
            }
          }
        }

      }
    }

  }
}

