.team {

  &__list {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    margin-left: -15px;
    margin-right: -15px;
    margin-bottom: 50px;

    > li {
      width: 33.32%;
      display: flex;
      flex-direction: column;
      margin: 0 0 30px 0;
      padding: 0 15px;
      @include media(sm) {
        width: 100%;
      }

      &.full {
        width: 100%;
        margin-bottom: 20px;

        > * {
          border-bottom: 1px solid $color-red;
          padding-bottom: 10px;
          color: $color-body;
        }
      }

    }

    &-title {
      font-size: 24px;
      font-weight: 600;
      color: $color-red;
    }

    &-subtitle {
      font-weight: normal;
      font-size: 16px;
      line-height: 1.4;
      color: $color-blue-light;
    }

    &-thumb {
      margin-bottom: 15px;
    }

  }



}
