.footer {
  $border-width: 2px;
  $border-color: #00A9B7;
  $border: $border-width solid $border-color;

  background-color: $color-blue;
  flex: 0 0 auto;

  &__toprow {

    display: flex;


    @include media(md) {
      flex-direction: column;
      align-items: center;
    }

    &-left {
      border-right: $border;
      flex: 1 1 auto;
      max-width: 490px;

      @include media(md) {
        border-right: unset;
      }
    }

    &-right {
      //width: 60%;
      display: flex;
      align-items: center;
      justify-content: center;
      flex: 1 1 auto;
      padding: 30px 0 30px 30px;
    }


  }

  &__botrow {
    display: flex;
    border-top: $border;
    position: relative;

    &::before {
      content: '';
      display: block;
      position: absolute;
      top: -#{$border-width};
      background-color: $border-color;
      left: -999px;
      right: -999px;
      height: $border-width;

    }

    &-left,
    &-right {
      display: flex;
      align-items: center;
      min-height: 200px;
    }

    &-left {
      flex: 1 1 auto;
      border-right: $border;

      @include media(md) {
        border-right: unset;
        border-bottom: $border;
      }
    }

    &-right {
      flex: 1 1 auto;
      max-width: 440px;
      display: flex;
      justify-content: center;
      align-items: center;
      padding-left: 15px;

      > div {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
      }

    }

    @include media(md) {
      text-align: center;
      flex-direction: column;
    }

  }

  &__text {
    padding: 35px 30px 35px 0px;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    color: $color-blue-light;

    @include media(md) {
      padding: 35px 20px 40px 15px;
        font-size: 18px;
        line-height: 1.5;
        letter-spacing: -0.06em;
    }
  }

  &__btngroup {
    padding: 25px 0;
    display: flex;
    align-items: center;
    position: relative;
    border-top: $border;

    @include media(md) {
      flex-direction: column;
    }

    &::before {
      content: '';
      display: block;
      position: absolute;
      height: $border-width;
      background-color: $border-color;
      top: -#{$border-width};
      left: -9999px;
      right: 0;

      @include media(md) {
        left: unset;
        right: unset;
        width: 300vw;
      }
    }

    &::after {
      @include media(md) {
        content: '';
        display: block;
        position: absolute;
        height: $border-width;
        background-color: $border-color;
        top: 100%;
        left: unset;
        right: unset;
        width: 300vw;
      }
    }

    .btn {
      border-color: $color-blue-light;
      color: #fff;

      @include media(md) {
        width: 50%;
        margin: 0;
        padding: 0;
      }

      &:not(.btn-opacity) {
        background-color: $color-blue-light;
      }

      &:hover {
        background-color: transparent;
        border-color: $color-blue-light;
        color: #fff;
      }

      &-opacity {

        &:hover {
          border-color: $color-blue-light;
          background-color: $color-blue-light;
        }

      }

      &:not(:last-child) {
        margin: 0 25px 0 0;

        @include media(md) {
          margin: 0 0 15px;
        }
      }
    }


  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 18px;
    color: $color-blue-light;
    max-width: 630px;

    a {
      color: $color-blue-light;
      transition: opacity 0.2s linear;

      &:hover {
        opacity: 0.45;
      }
    }

    > li {
      width: 50%;
      position: relative;
      padding: 0 0 0 30px;
      @include media(md) {
        padding: 0 0 0 20px;

      }
      &::before {
        $size: 8px;

        content: '';
        display: block;
        position: absolute;
        width: $size;
        height: $size;
        border-radius: 50%;
        border: 2px solid $color-blue-light;
        left: 0;
        top: 5px;

      }

      &:not(:last-child) {
        margin: 0 0 25px 0;
      }
    }
  }

  &__logolist {
    padding: 30px 0;


    &-title {
      text-transform: uppercase;
      color: $color-blue-light;
      font-weight: 600;
      font-size: 16px;
      line-height: 1.3;
      margin: 0 0 30px 0;
    }

    &-list {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      @include media(md) {
        justify-content: center;
      }
      > li {
        display: flex;
        align-items: center;

        @include media(md) {
          margin: 0 22px 20px 0;
        }
        &:not(:last-child) {
          margin: 0 22px 0 0;

          @include media(md) {
            margin: 0 22px 20px 0;
          }
        }

        a {

        }

      }
    }


  }

  &__socials {
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 330px;
    margin: 0 0 28px 0;

    > li {
      display: flex;
      align-items: center;
      justify-content: center;
    }

    svg,
    img {
      transition: opacity 0.2s linear;
    }

    a {
      &:hover {
        img,
        svg {
          opacity: .4;
        }
      }
    }

  }

  &__copyright {
    font-weight: normal;
    font-size: 16px;
    line-height: 1.4;
    color: $color-blue-light;
    max-width: 385px;
    text-align: center;
  }

  &__bottom {
    $background: #fff;

    background-color: $background;
    text-align: center;
    padding: 25px 0;
    font-weight: normal;
    font-size: 13px;
    line-height: 1.3;
    color: $color-blue-light;
    position: relative;
    z-index: 1;

    &::after {
      content: '';
      display: block;
      position: absolute;
      z-index: -1;
      background-color: $background;
      top: 0;
      bottom: 0;
      left: -999px;
      right: -999px;
    }
  }

}


