.header {
  padding: 35px 0;
  margin: 0 0 30px 0;
  position: relative;
  z-index: 3;
  @include media(md) {
    position: fixed;
    width: 100%;
    background-color: $backgroundColor-body;
  }

  &__row {
    $left-width: 240px;
    $left-width-lg: 175px;
    $left-width-md: 140px;

    display: flex;
    align-items: center;
    @include media(md) {
      justify-content: space-around;

    }



    &-left {
      width: $left-width;
      @include media(lg) {
        width: $left-width-lg;
      }

      @media (max-width: 1080px) {
        width: $left-width-md;
      }
      @include media(md) {
        width: 50%;

      }
    }

    &-right {
      width: calc(100% - #{$left-width});
      display: flex;
      align-items: center;
      justify-content: flex-end;
      @include media(lg) {
        width: calc(100% - #{$left-width-lg});
      }

      @media (max-width: 1080px) {
        width: calc(100% - #{$left-width-md});
      }
      @include media(md) {
        width: auto;
      }
    }


    &.active, &:hover {
      text-decoration: none;
      color: $color-yellow-light;
    }
  }

  &__button {
    margin: 0 0 0 60px;
    @include media(xl) {
      margin: 0 0 0 15px;
    }
    @include media(md) {
      display: none;
    }

    .btn {
      padding: 0 13px;
      text-align: left;
      white-space: nowrap;
      @include media(lg) {
        font-size: 14px;
        height: 40px;
      }
      @media (max-width: 1080px) {
        font-size: 12px;
      }

      svg {
        margin: 0 0 0 10px;
        transition: all 0.15s linear;
        @media (max-width: 1080px) {
          height: 10px;
          margin: 0 0 0 4px;
        }
      }

      &:hover {
        svg {
          transform: translateX(4px);
        }
      }
    }
  }

  &__menu {

    @include media(md) {
      display: block;
      position: unset;
      background: #fff;
      width: 100%;
      height: 100%;
    }

    &-list {
      display: flex;

      @include media(md) {
        flex-direction: column;
        position: absolute;
        right: 0;
        display: none;
        top: 100%;
        left: 0;
        padding: 0;
        background: $color-body;
        width: 75%;
        height: 100vh;

      }

      > li {
        font-weight: normal;
        font-size: 16px;
        letter-spacing: -0.02em;
        position: relative;
        z-index: 1;
        @include media(md) {
          display: block;
          float: none;
        }

        &.current_page_item {
          > a {

            &:not(:hover) {
              color: $color-red;

              &::before {
                display: block;
                background-color: $color-yellow-light;
                z-index: -1;
              }
            }

          }
        }

        &::before {
          content: '';
          display: none;
          position: absolute;
          z-index: -1;
          border-radius: 0px 10px 0px 0px;
          background-color: $color-yellow-light;
          left: 0;
          right: 0;
          top: 0;
          bottom: 0;


        }

        &:hover {

          &::before {
            display: block;
          }

          > ul {
            display: block;
            @include media(md) {
              display: none;
            }
          }

          > a {
            color: #fff;

            &::before {
              display: block;
              background-color: $color-yellow-light;

              @include media(md) {
                background-color: unset;
              }
            }
          }
        }

        > a {
          color: $color-blue;
          text-transform: lowercase;
          position: relative;
          z-index: 3;
          display: block;
          padding: 12px 15px;
          white-space: nowrap;
          @include media(md) {
            pointer-events: none;
            padding: 30px;
            color: $color-white;
          }

          &::after,
          &::before {
            content: '';
            display: none;
            position: absolute;
          }

          &::before {
            $size: 18px;
            $position: 7px;

            width: $size;
            height: $size;
            border-radius: 50%;
            background-color: $color-red;
            top: $position;
            right: $position;
            display: none;
            z-index: -1;

            @include media(md) {
              $position: 30px;

              display: block;
              position: absolute;
              left: unset;
              top: $position;
              right: $position;
              content: '';
              background-image: url(/wp-content/themes/redesign/assets/images/left_menu_arrow_red.svg);
              background-size: 100%;
              background-color: unset;
              height: 28px;
              width: 28px;
              object-fit: contain;
              transition: all .3s ease-in-out;
            }
          }

          &::after {
            $size: 18px;
            $position: 7px;

            width: $size;
            height: $size;
            border-radius: 50%;
            background-color: $color-red;
            top: $position;
            right: $position;
            display: none;
            z-index: -1;

          }

        }

        &.menu-item {
          &-has-children {
            &:hover {
              > a {
                &::after {
                  display: block;

                  @include media(md) {
                    display: none;
                  }
                }
              }
            }
          }
        }

        &:nth-child(1),
        &:nth-child(2),
        &:nth-child(3) {
          > ul {
            right: auto;
            left: 0;
          }
        }

        > ul {
          $gutter: 15px;

          display: none;
          position: absolute;
          flex-wrap: wrap;
          background: #fff;
          border: 1px solid $color-yellow-light;
          left: auto;
          right: 0;
          top: 100%;
          padding: 25px 30px;
          width: 480px;
          column-count: 2;
          column-gap: 40px;
          overflow: hidden; /* Fix for firefox and IE 10-11  */
          @include media(md) {
            display: none;
          }

          a {
            transition: color 0.14s linear;
          }

          > li {
            -webkit-column-break-inside: avoid;
            page-break-inside: avoid; /* Makes effect only in Firefox */
            break-inside: avoid; /* IE10+, Opera 11.112.1 */

            &:not(:last-child) {
              border-bottom: 1px solid $color-gray-light;
              margin: 0 0 10px 0;
            }

            > a {
              color: $color-red;
              font-weight: normal;
              font-size: 14px;
              line-height: 1.4;
              padding: 0px 0 10px 0;
              display: block;

              &:hover {
                color: $color-blue;
              }
            }

            .children {
              padding: 0px 0 12px 20px;

              li {
                line-height: 1.3;
              }


              a {
                font-weight: normal;
                font-size: 14px;
                line-height: 1.4;
                //color: #C4C4C4;

                &:hover {
                  color: $color-blue;
                }
              }
            }
          }
        }

        .sub-menu {
          @include media(md) {
            column-count: 1;
            //display: block;
            width: 100%;
            position: relative;
          }
        }

        &.mob-active {

          a {
            &::before {
              @include media(md) {
                background-image: url(/wp-content/themes/redesign/assets/images/left_menu_arrow_yellow.svg);
                //margin: 30px;
                transform: rotateZ(90deg);
              }
            }
          }
        }

      }

    }

    &-logo-mobile {
      display: none;

      @include media(md) {
        position: absolute;
        right: 0;
        top: 0;
        display: none;
        left: 0;
        padding: 0;
        background: #1C3360;
        width: 75%;
        height: 100%;

      }

      img {
        height: 100%;
        @include media(md) {
          width: 80%;
          margin: 0 auto;
        }
        }

    }

    &-button-mobile.menu-item {
      //position: absolute;
      //right: 0;
      //top: 0;
      display: none;
      @include media(md) {
        display: block;
        left: 0;
        padding: 0;
        //background: #1C3360;
        width: 75%;
        height: auto;
        text-transform: uppercase;
        margin: 0 auto;
        &:before {
          content: unset;

        }
      }

      &:before {
        content: unset;
      }

      &:after {
        background: linear-gradient(100.3deg, #FCC62B 34.05%, #DA1B5A 86.98%);
        position: absolute;
        content: "";
        border-radius: 999px;
        width: 100%;
        height: 100%;
        display: block;
        top: 0;
      }

      a {

          &:before {
            @include media(md) {

              content: unset;
          }
          }
        @include media(md) {
pointer-events: unset;
          padding: 20px;
          display: flex;
          justify-content: space-evenly;
          align-items: center;
          text-transform: uppercase;
          font-size: 14px;
        }
      }
    }
  }

  &__btnmenu {
    display: none;
    @include media(md) {
      display: block;
      color: $color-yellow-light;
      font-size: 3.5em;
      &:active, &:hover {
        color: $color-yellow-light;

      }
    }
  }

}
